import React, { useLayoutEffect, useRef, useState } from 'react'

import { link } from 'constant'

import { SEO } from 'components/feature'
import { LayoutMain, Main } from 'components/layouts'
import { Header, NotFound, Footer } from 'containers'

const browser = typeof window !== 'undefined' && window

const NotFoundPage = ({ location }: any) => {
  const [isChecking, setIsChecking] = useState(true)

  useLayoutEffect(() => {
    let _isChecking = false

    if (new RegExp(/^\/juicebox/i).test(location.pathname)) {
      _isChecking = true
      window.location.replace(link.NFT_HOST_ROMAN)
    }

    if (new RegExp(/^\/donate/i).test(location.pathname)) {
      _isChecking = true
      window.location.replace('/donate')
    }

    setIsChecking(_isChecking)
  }, [location.pathname])

  if (isChecking) {
    return null
  }

  return (
    browser && (
      <LayoutMain>
        <SEO title='Page not found!' description='Sorry, the page you were looking for does not exist. (404)' />
        <Header />
        <Main role='main'>
          <NotFound />
        </Main>
        <Footer />
      </LayoutMain>
    )
  )
}

export default NotFoundPage
